let cachePrice = '';
let cachePay = '';
export default {
    data() {
        return {}
    },
    props: {
        info: { //区分模块用的
            type: Object,
            default: {}
        }
    },
    methods: {
        inputPrice(val) {
            cachePrice = val.detail.value || 0;
            this.$emit('input', {
                value: {
                    price: cachePrice,
                    pay: cachePay
                },
                info: this.info
            })
        },
        inputPay(val) {
            cachePay = val.detail.value || 0;
            this.$emit('input', {
                value: {
                    price: cachePrice || 0,
                    pay: cachePay
                },
                info: this.info
            })
        }
    },
}
